import React from "react";
import "./styles.css";

const ConfirmBox = ({ setAction, showPrompt, item_name }) => {
  return (
    <div className={showPrompt ? "confirm-box" : "confirm-box-hidden"}>
      <p>{item_name.replace(/^\w/, (c) => c.toUpperCase())}</p>
      <div className="btns">
        <button onClick={() => setAction("cancel")}>Cancel</button>
        <button onClick={() => setAction("buy", item_name)}>Buy</button>
        <button onClick={() => setAction("sell", item_name)}>Sell</button>
      </div>
    </div>
  );
};

export default ConfirmBox;
